<template>
  <div id="app">
    <div class="agreement" v-if="!isAgree">
      <p class="argeement-condition">
        Я розумію що данний сайт НЕ є достовірним джерелом інформації. 
      </p>
      <button @click="agree" class="argeement-button">Згоден перевіряти усі питання власноруч</button>
    </div>

    <div v-if="isAgree" class="position">
      <p class="question">Запитання №{{secretNumber}} з {{testData.length+1}}</p>
      <div class="goTo">
        <p>Перейдіть до запитання: </p>
        <input type="number" min="0" max="181" v-model.number="secretNumber" placeholder="Номер запитання">
      </div>
    </div>

    <div class="test" v-if="isAgree">
      <h1 class="test_name"><b>{{testData[secretNumber].question}}</b></h1>
      <!-- <button @click="checkAnswer(testData[secretNumber].answers[0])" v-bind:class="[letterOfRightAnswer == 'a' ? 'button-true' : '', letterOfRightAnswer != 'a' && letterOfRightAnswer != '' ? 'button-false':'','test_answer']">{{testData[secretNumber].answers[0]}}</button>
      <button @click="checkAnswer(testData[secretNumber].answers[1])" v-bind:class="[letterOfRightAnswer == 'b' ? 'button-true' : '', letterOfRightAnswer != 'b' && letterOfRightAnswer != '' ? 'button-false':'','test_answer']">{{testData[secretNumber].answers[1]}}</button>
      <button @click="checkAnswer(testData[secretNumber].answers[2])" v-bind:class="[letterOfRightAnswer == 'c' ? 'button-true' : '', letterOfRightAnswer != 'c' && letterOfRightAnswer != '' ? 'button-false':'','test_answer']">{{testData[secretNumber].answers[2]}}</button>
      <button @click="checkAnswer(testData[secretNumber].answers[3])" v-bind:class="[letterOfRightAnswer == 'd' ? 'button-true' : '', letterOfRightAnswer != 'd' && letterOfRightAnswer != '' ? 'button-false':'','test_answer']">{{testData[secretNumber].answers[3]}}</button>
      <button @click="checkAnswer(testData[secretNumber].answers[4])" v-bind:class="[letterOfRightAnswer == 'e' ? 'button-true' : '', letterOfRightAnswer != 'e' && letterOfRightAnswer != '' ? 'button-false':'','test_answer']">{{testData[secretNumber].answers[4]}}</button> -->

      <button 
        v-for="(item, index) in testData[secretNumber].answers" 
        :key="item" 
        @click="checkAnswer(testData[secretNumber].answers[index])"
        v-bind:class="[(currentAnswer == testData[secretNumber].rightAnswer && testData[secretNumber].answers[index] == testData[secretNumber].rightAnswer) ? 'button-true' : '', (currentAnswer != testData[secretNumber].rightAnswer && testData[secretNumber].answers[index] != testData[secretNumber].rightAnswer && currentAnswer !='') ? 'button-false':'','test_answer']"
        >{{testData[secretNumber].answers[index]}}</button>
    </div>
    <button v-if="isAgree" @click="showStat" class="stat_button">Звіт</button>
    <button v-if="isAgree" @click="backQuestion" class="back_question arrows">⟸ Назад</button>
    <button v-if="isAgree" @click="nextQuestion" class="next_question arrows">Вперед ⟹</button>
    <button v-if="isAgree" @click="nextQuestion" class="next_question arrows">Вперед ⟹</button>

    <div v-if="isStat" @click="closeStat" class="stat"> 
      <div class="window">
        <h2 class="stat-text">Ваш результат:</h2>
        <p class="stat-text">В данному буклеті {{testData.length}} питань</p>
        <p class="stat-text">Пройдено запитань:<br>{{secretNumber}}</p>
        <p class="stat-text">Відповіли правильно:<br>{{rightAnswers}}</p>
        <p class="stat-text">Допустили помилок:<br>{{wrongAnswers}} </p>
        <p v-if="rightAnswers||wrongAnswers" class="stat-text">Результат: {{((rightAnswers/(rightAnswers+wrongAnswers))*100).toFixed(1)}} %</p>
      </div>
    </div>
  </div>
</template>

<script>
// import testData from './newData2019.json'
import testData from '../newData.json';
import PincodeInput from 'vue-pincode-input';
export default {
  name: 'App', 
  data() {
    return {
      isAgree: false, 
      testData,
      secretNumber: 0,
      isQuestionPassed: false,
      restOfQuestions: testData.length,
      rightAnswers: 0, 
      wrongAnswers: 0, 
      percentage: 0,
      letterOfRightAnswer: '', 
      isStat: false,
      PincodeInput,
      currentAnswer: ''
    }
  }, 
  methods: {
     showStat() {
      this.isStat = !this.isStat; 
    },
    closeStat() {
      this.isStat = false; 
    }, 
    agree() {
      // this.shuffle(this.testData); 
      this.isAgree = true;
    },
    shuffle(array) {
      var currentIndex = array.length, temporaryValue, randomIndex;
      // While there remain elements to shuffle...
      while (0 !== currentIndex) {
        // Pick a remaining element...
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex -= 1;
        // And swap it with the current element.
        temporaryValue = array[currentIndex];
        array[currentIndex] = array[randomIndex];
        array[randomIndex] = temporaryValue;
      }
      return array;
    }, 
    backQuestion() {
      if(this.secretNumber >0) {
        this.secretNumber -=1; 
       }  else alert('Ви на першому запитанні.'); 
    },
    nextQuestion() {
      if (this.secretNumber == this.restOfQuestions-1){
        alert ('Тестування завершено.'); 
      } else {
         this.secretNumber +=1; 
         this.currentAnswer = '';
        }
    }, 
    checkAnswer(a) {
      this.isQuestionPassed = true; 
      this.restOfQuestions -= 1;
    
      if(a == this.testData[this.secretNumber].rightAnswer) {
        this.rightAnswers += 1; 
        this.currentAnswer = a;         
      } else {
        this.wrongAnswers += 1; 
        this.currentAnswer = a;
      }
    }
  }
}
</script>

